import React from "react";
import { useHistory } from "react-router-dom";

import mt1 from "../img/mock_mt_1.jpeg";
import mt2 from "../img/mock_mt_2.png";
import mt3 from "../img/mock_mt_3.png";
import virus1 from "../img/mockup1.png";
import virus2 from "../img/mockup2.png";
import Fade from "react-reveal/Fade";

const Project2 = () => {
  const { push } = useHistory();
  const history = useHistory();

  window.onscroll = function () {
    scrollFunction();
  };
  function linkClick() {
    var li = document.getElementById("link");
    var lis = document.getElementById("links");

    let width = window.innerWidth;
    if (width < 769) {
      var li = document.getElementById("link");
      li.style.width = "25%";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      var li = document.getElementById("link");

      if (document.getElementById("link") !== "null") {
        var li = document.getElementById("link");

        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "0.3vw";
          li.style.height = "1vw";
          lis.style.display = "none";
        } else if (li.style.width === "0.3vw") {
          li.style.width = "90%";
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    if (width < 769) {
      var li = document.getElementById("link");
      li.style.width = "100%";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        var li = document.getElementById("link");
        li.style.width = "0.3vw";

        li.style.height = "1vw";
        var lis = document.getElementById("links");
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        var li = document.getElementById("link");
        li.style.width = "90%";
        var lis = document.getElementById("links");
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }

  function handleClick() {
    history.push("/aliceljungholm");
  }
  function handleClickProject() {
    history.push("/aliceljungholm/Project");
  }

  function handleClickAbout() {
    history.push("/aliceljungholm/about");
  }
  function handleHomeClick() {
    history.push("/aliceljungholm");
  }

  function handleClickProject() {
    history.push("/aliceljungholm/Project");
  }
  return (
    <div className="pro">
      <div id="link">
        <Fade left>
          <div id="links">
            <a id="menuLink" onClick={handleHomeClick}>
              Home
            </a>{" "}
            |{" "}
            <a id="menuLink" onClick={handleClickProject}>
              Projects
            </a>{" "}
            |{" "}
            <a id="menuLink" onClick={handleClickAbout}>
              About
            </a>{" "}
            |{" "}
            <a id="menuLink" href="#contact-me">
              Contact
            </a>
          </div>
        </Fade>
      </div>

      <h1 className="header1">COMBAT COVID</h1>

      <Fade bottom>
        <div className="cont">
          <div id="virustrio">
            <img alt="Mockup" id="virus1" src={virus1}></img>
            <img alt="Mockup" id="virus2" src={virus2}></img>
          </div>

          <div id="thesisText">
            <h3>Summary</h3>
            <p className="leftText">
              Combat Covid on Campus (CCC) was created by me and two other
              students. The app took inspiration from “Popular Times” on Google
              and tackled the question “How to Combat Covid on Campus”. CCC
              allowed users to check-in to campus before going, letting the
              people who wanted to avoid crowds know when campus was busy. The
              app also allowed for potential contract tracing for the COVID-19
              virus. CCC was designed in Figma and later implemented using
              React.
            </p>
            <h3>More Information</h3>
            <p className="leftText">More Info comming soon!</p>
          </div>
        </div>
        <div id="backH">
          <a id="BackHome" onClick={handleClickProject}>
            Back to all Projects
          </a>
        </div>
      </Fade>
    </div>
  );
};
export default Project2;
